"use client"

import { useEffect, useMemo, useRef, useState } from "react"
import { useRouter } from "next/navigation"
import { RecaptchaVerifier, linkWithPhoneNumber } from "firebase/auth"
import { auth } from "@/lib/firebase"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Card } from "@/components/ui/card"
import { COUNTRIES } from "./countries"
import { Select, SelectContent, SelectItem, SelectTrigger } from "@/components/ui/select"
import { useToast } from "@/hooks/use-toast"
import { ArrowLeft, Check, Smartphone } from "lucide-react"

export default function PhoneVerificationPage() {
  const router = useRouter()
  const { toast } = useToast()

  const [country, setCountry] = useState("TR")
  const [phone, setPhone] = useState("")
  const [smsSending, setSmsSending] = useState(false)
  const recaptchaRef = useRef<RecaptchaVerifier | null>(null)

  const selectedCountry = useMemo(() => {
    return COUNTRIES.find((c) => c.id === country) || COUNTRIES[0]
  }, [country])

  useEffect(() => {
    const unsub = auth.onAuthStateChanged((user) => {
      if (!user) {
        router.replace("/login")
      }
    })
    return () => {
      unsub()
      if (recaptchaRef.current) {
        try {
          recaptchaRef.current.clear()
        } catch {}
      }
    }
  }, [router])

  const normalizePhoneE164 = (raw: string) => {
    const dial = selectedCountry.dial
    const dialDigits = dial.replace("+", "")

    const v = raw.trim().replace(/\s+/g, "")
    const cleaned = v.replace(/[^\d+]/g, "")
    if (!cleaned) return ""

    if (cleaned.startsWith("+")) return cleaned

    let national = cleaned
    if (national.startsWith("0")) national = national.replace(/^0+/, "")

    if (national.startsWith(dialDigits)) {
      return `+${national}`
    }

    return `${dial}${national}`
  }

  const ensureRecaptcha = () => {
    if (recaptchaRef.current) return recaptchaRef.current
    const verifier = new RecaptchaVerifier(auth, "recaptcha-container", { size: "invisible" })
    recaptchaRef.current = verifier
    return verifier
  }

  const fullPhone = useMemo(() => {
    const cc = selectedCountry.dial
    const p = phone.trim().replace(/\s+/g, "")
    if (!p) return cc
    if (p.startsWith("+")) return p
    return `${cc}${p}`
  }, [selectedCountry, phone])

  const sendSms = async () => {
    if (typeof window === "undefined") return
    const user = auth.currentUser
    if (!user) {
      router.replace("/login")
      return
    }

    const e164 = normalizePhoneE164(fullPhone)
    if (!e164 || e164.length < 10) {
      toast({
        variant: "destructive",
        title: "Ge√ßersiz numara",
        description: "L√ºtfen ge√ßerli bir telefon numarasƒ± gir.",
      })
      return
    }

    setSmsSending(true)
    try {
      const verifier = ensureRecaptcha()
      await verifier.render()
      const confirmation = await linkWithPhoneNumber(user, e164, verifier)
      if (typeof window !== "undefined") {
        sessionStorage.setItem("phone_e164", e164)
        sessionStorage.setItem("phone_verificationId", confirmation.verificationId)
      }
      router.push("/profile/settings/phone/otp")
    } catch (e: any) {
      const code = e?.code as string | undefined
      let message = e?.message || "SMS g√∂nderilemedi."
      if (code === "auth/operation-not-allowed") message = "Phone provider'ƒ± a√ß."
      else if (code === "auth/captcha-check-failed") message = "reCAPTCHA doƒürulanamadƒ±."
      else if (code === "auth/invalid-phone-number") message = "Ge√ßersiz telefon numarasƒ±."
      else if (code === "auth/too-many-requests") message = "√áok fazla deneme yapƒ±ldƒ±, l√ºtfen bekleyin."

      toast({
        variant: "destructive",
        title: "G√∂nderilemedi",
        description: `${message}${code ? ` (${code})` : ""}`,
      })
    } finally {
      setSmsSending(false)
    }
  }

  const Stepper = () => (
    <div className="flex items-center justify-center gap-3">
      {[0, 1, 2].map((i) => {
        const isActive = i === 0
        return (
          <div
            key={i}
            className={`h-9 w-9 rounded-full flex items-center justify-center text-sm font-semibold border-2 ${
              isActive ? "bg-black text-white border-black" : "border-muted text-muted-foreground"
            }`}
          >
            {isActive ? <Check className="h-4 w-4" /> : i + 1}
          </div>
        )
      })}
    </div>
  )

  return (
    <div className="min-h-screen bg-background">
      <div className="mx-auto max-w-lg px-4 py-6 space-y-6">
        <div className="flex items-center justify-between">
          <Button variant="ghost" size="icon" onClick={() => router.back()} className="h-9 w-9">
            <ArrowLeft className="h-5 w-5" />
          </Button>
          <Stepper />
          <div className="w-9" />
        </div>

        <Card className="p-6 sm:p-8 shadow-lg">
          <div className="space-y-6">
            <div className="space-y-2">
              <h1 className="text-2xl sm:text-3xl font-bold">Telefon numaranƒ± gir</h1>
              <p className="text-sm text-muted-foreground">Numaranƒ± doƒürulamak i√ßin SMS g√∂ndereceƒüiz.</p>
            </div>
            <div className="space-y-2">
              <Label>Telefon</Label>
              <div className="flex items-center gap-2">
                <Smartphone className="h-4 w-4 text-muted-foreground" />

                <div className="flex w-full">
                  <Select value={country} onValueChange={setCountry}>
                    <SelectTrigger className="h-12 w-[150px] rounded-l-lg rounded-r-none border-2 border-r-0 bg-background px-3">
                      <div className="flex items-center gap-2">
                        <span>{selectedCountry.flag}</span>
                        <span className="font-medium">{selectedCountry.dial}</span>
                      </div>
                    </SelectTrigger>
                    <SelectContent>
                      {COUNTRIES.map((c) => (
                        <SelectItem key={c.id} value={c.id}>
                          <span className="mr-2">{c.flag}</span>
                          <span className="mr-2">{c.name}</span>
                          <span className="text-muted-foreground">({c.dial})</span>
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                  <Input
                    value={phone}
                    onChange={(e) => setPhone(e.target.value)}
                    placeholder="5xx xxx xx xx"
                    disabled={smsSending}
                    className="h-9 text-base rounded-l-none rounded-r-lg border-2 border-l-0"
                    inputMode="tel"
                    autoComplete="tel"
                  />
                </div>
              </div>
            </div>
            <Button
              className="w-full h-12 bg-black text-white hover:bg-black/90"
              onClick={sendSms}
              disabled={smsSending || !phone.trim()}
            >
              {smsSending ? "G√∂nderiliyor..." : "ƒ∞leri"}
            </Button>
            <Button variant="ghost" className="w-full h-11" onClick={() => router.back()}>
              Geri d√∂n
            </Button>
          </div>
        </Card>
      </div>
      <div
        id="recaptcha-container"
        className="pointer-events-none opacity-0 absolute -left-[9999px] -top-[9999px]"
      />
    </div>
  )
}
